@use '../base/variables' as v;
@use '../base/mixins' as m;



.contenido-nosotros{
    @include m.tablets { 
        @include m.grid(2, 4rem);
    }

    .imagen{
        margin-bottom: 5rem;
        @include m.tablets {
            margin: 0 2rem; 
        }


        img{
            transform: rotate(-6deg);
            border: 3rem solid white;
            box-shadow: 9px 10px 5px -7px rgb(0 0 0 / .35);
        }
    }

    .texto{
        blockquote{
            margin: 0;
            padding: 1rem;
            border-left: .5rem solid v.$primario;
            font-size: 2rem;
        }
    }
}