@use '../base/variables' as v;
@use '../base/mixins' as m;


.header{
    padding: 5rem;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;

    @include m.telefono { 
        padding: 4rem;
    }

    @include m.tablets { 
        padding: 3rem;
        min-height: 70rem;
    }

    @include m.desktop{
        padding: 2rem ;
    }

    &::before{
        content: '';
        background-color:v.$negro;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: .6;
    }


    .contenido-header{
        position: relative;

        h1{
            text-align: center;
            color: v.$blanco;
            text-transform: uppercase;
            margin-top: 5rem;
            @include m.tablets { 
                margin-top:10rem;
            }
        }
    }
}



