@use '../base/mixins' as m;
@use '../base/variables' as v;


.nuestro-menu{
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, v.$primario, v.$primario);
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, 100%;
    padding: 15rem 5rem 20rem 5rem;

    @include m.tablets { 
        padding:15rem 0 20rem 0;
        background-size: 100%;
    }

    @include m.desktop { 
        background-size:50%, 50%, 100%;
}
}


.grid-menu{
    @include m.tablets { 
        @include m.grid(2, 8rem);
    }
}

.menu{
    h3{
        padding-top: 8.5rem;
        color:v.$blanco
    }


    li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            color:v.$blanco;
            font-size: 2.2rem;
            line-height: 3rem;
            margin: 0 0 2rem 0;

            span{
                display: block;
                color: v.$secundario;
            }
        }

        .precio{
            font-size: 4rem;
            font-weight: 700;
        }
    }
}